@import '~antd/dist/antd.css';
html{
  
}
body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  
}
iframe{
  display: none ;
}
#root{background: #F4F4F4;height: 101%;}
//取消双击选中文字，字体为微软雅黑
div,span{
  -moz-user-select:none;/*火狐*/
  -webkit-user-select:none;/*webkit浏览器*/
  -ms-user-select:none;/*IE10*/
  -khtml-user-select:none;/*早期浏览器*/
  user-select:none;
  font-family: Microsoft YaHei;
  color: #363636;
  font-size: 14px;
  box-sizing: border-box;
}
.position{
  position: relative;
}
.cursor{
  cursor: pointer;
}
.width{
  width: 1200px;
  margin: 0 auto;
}
.bold{
  font-weight: 600;
}
.font12{
  font-size: 12px;
}
.font16{
  font-size: 16px;
}
.top10{
  margin-top: 10px;
}
.fleximg{
  display: flex;
  justify-content: center;
  align-items: center;
  img{
      width: 100%;
  }
}
.fleximgtop{
  display: flex;
  justify-content: center;
  align-items: flex-start;
  img{
      width: 100%;
  }
}
.flexl{
  display: flex;
  justify-content: flex-start;
  align-items: center;
}
.flexr{
  display: flex;
  justify-content: flex-end;
  align-items: center;
}
.flexb{
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.flexa{
  display: flex;
  justify-content: space-around;
  align-items: center;
}
.flexbl{
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
}
.flexll{
  display: flex;
  align-items: flex-start;
  justify-content: flex-start;
}
.flexcbl{
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: flex-start;
}
.fleximgc{
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
}
.flexcc{
  display: flex;
  flex-direction: column;
  justify-content:center;
  align-items: center;
}
.back{
  background: rgba($color: #000000, $alpha: 0.3);
  // height: 100vh;
}
.head{
  border-radius: 50%;
  overflow: hidden;
  img{
    width: 100%;
    height: 100%;
    object-fit: cover;
  }
}
.onemore{
  overflow: hidden;
  text-overflow:ellipsis;
  white-space: nowrap;
}
.back-color{
  background: #F4F4F4;
}
.color1{
  color: #363636;
}
.color2{
  color: #666666;
}
.color3{
  color: #999999;
}
.colorw{color: #FFFFFF;}
.pagination{
  .ant-pagination-item-active{
      border-color: #ff8800;
      background:#ff8800;
      a{
          color: #ffffff;
      }
  }
  
  .ant-pagination-item a:hover{
      color: #ff8800;
  }
  .ant-pagination-item-active a:hover{
      color: #ffffff;
  }
  .ant-pagination-prev:hover .anticon-left{
      color: #ff8800;
  }
  .ant-pagination-next:hover .anticon-right{
      color: #ff8800;
  }
  .anticon-right:hover{
      color: #ff8800;
  }
  .anticon-left:hover{
      color: #ff8800;
  }
}
.tableHeader{
  width: 870px;
  height: 50px;
  background: #304F97;
  >div{
      color: #FFFFFF;
      width: 290px;
      padding: 0 10px;
      text-align: center;
  }
}
.pointer{cursor: pointer;}
.row-active{
  background:#F4F4F4 !important;
}
.paginations{
  margin-top: 30px;
}
.tox-tinymce-aux {
  z-index: 709826033 !important;
}
.tox-tinymce-aux {
  z-index: 709826033 !important;
}
.tox-tinymce-inline {
  z-index: 709826032 !important;
}
// 资质问题样式
.cert-modal{
  .ant-modal{
    width: 0 !important;
    height: 0;
    background: #FFFFFF;
    left: -49px;
    top: 390px;
    opacity: 0;
    .ant-modal-header,.ant-modal-footer,.ant-modal-close{
      opacity: 0;
    }
  }
}
.ant-picker-footer{
  display: none
}
.wxlogin-modal{
  .ant-modal{
    width: 400px;
    top:25%;
    .ant-modal-content{ 
      width: 400px;
      overflow: hidden;
      .ant-modal-body{
        padding:24px;
      }
      .ant-modal-header,.ant-modal-footer{
        display: none;
      }
    }
  }
  
}
.article-modal{
  // .delete-text{
  //   text-align: center;
  // }
  .ant-modal{
    width: 350px !important;
    top: 35%;
  }
  .ant-btn:hover, .ant-btn:focus{
    color: #304F97;
    border-color: #304F97;
  }
  .ant-btn-primary{
    background: #304F97;
    border-color: #304F97;
    span{
      color: #FFFFFF;
      // background: #304F97;
    }
  }
}
.ant-modal-mask{
  background:none
}
.ant-scrolling-effect{
  overflow-y: scroll !important;
  width: 100% !important;
}
#wxLogin{
  iframe{
    display: block;
  }
}
#certificate{
  .ant-form{
    height: 986px;
  }
  .ant-spin{
    top: 50%;
    transform: translateY(-50%);
  }
}
#certificate{
  .ant-spin-nested-loading{
    position: absolute;
    z-index: 9;
  }
}


//企业认证上传图片组件的样式
.upload{
  width: 100px;
  height: 100px;
  opacity: 0;
  span{
    display: inline-block;
    width: 100%;
    height: 100%;
    .ant-upload{
      width: 100%;
    height: 100%;
    }
  }
}
// 用户须知页面样式
.userAgreement{
  padding: 30px ;
  background: #F4F4F4;
  p{
    margin: 0;
    padding: 0;
  }
  .content{
    width: 1200px;
    padding: 30px 70px;
    margin: 0 auto;
    background: #FFFFFF;
    .title{
      text-align: center;
      font-size: 20px;
      line-height: 60px;
    }
    br{width: 100%;height: 50px;}
    .text{
      font-size: 14px;
      line-height: 50px;
    }
    .top{
      font-size: 14px;
      line-height: 50px;
      font-weight: bold;
    }
  }
}

//个人中心-我的推广h5页面
#article-detail-h5{
  padding: 0.5333rem 0.4267rem 0;
  background: #FFFFFF;
  overflow: hidden;
  .title{
    font-size: 0.5867rem;
    color: #363636;
    text-align: justify;
    line-height: 0.8533rem;
    font-weight: 600;
  }
  .article-detail-info{
    padding: 0.64rem 0 0.5333rem;
    border-bottom: 1px solid #F4F4F4;
    .from{
      font-size: 0.32rem;
      color: #999999;
    }
  }
  .news-content{
    width: 100%;
    margin-top: 0.64rem;
    overflow: hidden;
    img{
      width: 100%;
    }
  }
}
.foot-more{
  height: 1.8133rem;
  width: 100%;
  background: #FFFFFF;
  position: fixed;
  bottom: 0;
  left: 0;
  >div{
    width: 2.56rem;
    height: 0.7467rem;
    background: #304F97;
    border-radius: 2px;
    font-size: 0.32rem;
    color: #FFFFFF;
  }
}
.OSSUpload-div{
  width: 220px;
  height: 140px;
  position: absolute;
  top: 0;
  left: 0;
}

